<template>
<!--公共列表页-->
  <div class="ariticle-list">
    <h3 class="ariticle-list-title">{{subChannelName | prices | news | data | analysis | reports | crudeoil}}</h3>
    <ul class="">
      <li class="news-list" v-for="(item,index) in listData">
        <div class="clearfix">
          <h3 class="fl channel-news-title" @click="seeDetails(item.uuid)">{{item.title}}</h3>
          <span class="date fr">{{item.release_date | ddMMMyyyy}}</span>
        </div>
      </li>
    </ul>
    <el-pagination
      class="pagination"
      v-if="this.listData.length>20"
      layout="prev, pager, next"
      :page-size="20"
      @current-change="handleCurrentChange"
      :total="this.listData.length">
    </el-pagination>
    <SubChannelNewsDetails :visible.sync='detailsVisible' :detailsData = "detailsData"></SubChannelNewsDetails>
  </div>
</template>
<script>
  import SubChannelNewsDetails from '@/modules/SubChannelNewsDetails.vue'
  import {channelSubList,listDatasd,listDataParticulars} from '@/api/info'
    export default {
        name: "ariticle-list",
        data(){
          return {
              detailsVisible:false,
              subChannelName:'',
              detailsData:{},
              listData:[]
          }
        },
        components:{
          SubChannelNewsDetails
        },
        mounted(){
          this.subChannelName = this.$route.params.subName.toUpperCase().split('_').join(' ')
          this.initListData(1)
        },
      methods:{
        initListData(pageNum){
          listDatasd({channelName:this.subChannelName,channelMDesc:"CPY",pageNum:pageNum,pageSize:20}).then((res)=>{
            if(res.status == 0){
              this.listData = res.datas.infoList
            }
          })
        },
        seeDetails(id){
          listDataParticulars({infoUuid:id}).then((res)=>{
            if(res.status==0){
              this.detailsVisible = true
              this.detailsData = res.datas
            }
          })
        },
        handleCurrentChange(val){
          this.initListData(val)
        }
      }
    }
</script>

<style scoped>
  .ariticle-list-title{
    margin: 30px 0;
    font-size: 22px;

  }
  .channel-news-title{
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #333333;
    max-width: 731px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .news-list{
    width: 840px;
    margin-top: 20px;
  }
  .news-list:first-child{
    margin-top: 30px;
  }
  .channel-news-title{
    max-width: 731px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
  }
  .channel-news-title:hover{
    text-decoration: underline;
  }
  .pagination{
    margin: 20px auto;
  }
</style>



